

























import { mapState } from 'vuex';
import { baseURL, imgURl } from '../../common/config/index.js';
import { queryOrderList, confirmOrder, cancelOrder } from '@/request/API/index.js';
export default {
	data() {
		return {
			queryType: 1,
			imgURl: '',
			productList: [],
			currentTabs: 1,
			state: 1,
			pageNo: 1,
			pageSize: 10
		};
	},
	onLoad() {
		this.imgURl = imgURl;
		//获取订单列表
		this.queryOrderList(this.userId, this.queryType, this.state, this.pageNo, this.pageSize);
	},
	//上拉加载
	onReachBottom() {
		this.pageNo++;
		this.queryOrderList(this.userId, this.queryType, this.state, this.pageNo, this.pageSize);
	},
	computed: {
		...mapState(['userId', 'shopId'])
	},
	methods: {
		//确认订单
		confirm(id) {
			confirmOrder('', id, 2).then(res => {
				if (res.data.code == 0) {
					uni.showToast({
						title: '确认订单成功',
						icon: 'none',
						duration: 1000
					});
					this.pageNo = 1;
					this.productList = [];
					this.queryOrderList(this.userId, this.queryType, this.state, this.pageNo, this.pageSize);
				}
			});
		},
		//取消订单
		cancel(id) {
			cancelOrder(id).then(res => {
				if (res.data.code == 0) {
					this.pageNo = 1;
					this.productList = [];
					this.queryOrderList(this.userId, this.queryType, this.state, this.pageNo, this.pageSize);
				}
			});
		},
		//查询订单列表
		queryOrderList(userId, queryType, state, pageNo, pageSize) {
			queryOrderList(userId, queryType, state, pageNo, pageSize).then(res => {
				if (res.data.code == 0) {
					if (res.data.data.list.length == 0) {
						uni.showToast({
							title: '没有更多数据了',
							icon: 'none',
							duration: 1000
						});
					}
					this.productList = [...this.productList, ...res.data.data.list];
				}
			});
		},
		tabs(e) {
			this.productList = [];
			this.state = e;
			this.currentTabs = e;
			this.queryOrderList(this.userId, this.queryType, this.state, this.pageNo, this.pageSize);
		}
	}
};
